.section {
    background: white;
    box-shadow: 0 0 5px #ddd;
    margin: 10px 0;
}

.section__title {
font-size: 20px;
margin: 0;
}
.section__header {
padding: 20px;
border-bottom: 1px solid #ddd;
display: grid;
grid-template-columns: auto auto;
grid-gap: 20px;
justify-content: space-between;
align-items: center;
}

@media (max-width: 767px) {
.section__header {
    grid-template-columns: 1fr;
}
}

.section__container {
background-color: white;
padding: 1%;
}